// Named Export (exporting/importing by names)
// Default Export (at most 1 export)

// EXPORTING AS A WHOLE

const add = (a, b) => a+b;
const name = "Ikrom";
const square = (x) => x*x;

export {add, name, square as default}


/* EXPORTING INDIVIDUALLY

export const add = (a, b) => a+b;
export const name = "Ikrom"
const square = (x) => x*x

export default square

*/